import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';

/* Custom Modules */
import { PipeModule } from '../../Pipes/pipe.module';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { MeetComponent } from './meet.component';
import { MeetSearchComponent } from './meet-search/meet-search.component';

/* Directives */
import { MeetDirective } from '../../Directives/Meet/meet.directive';
import { MeetDirectiveWMS1 } from '../../Directives/Meet/meet-wms1.directive';
import { MeetDirectiveWMS2I } from '../../Directives/Meet/meet-wms2i.directive';
import { DirectivesModule } from '../../Directives/directives.module';

/* Route */
import { MEET_ROUTES } from '../../Routes/meet.routes';

@NgModule({
	imports: [
		MEET_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		PipeModule,
		FooterModule,
		InfiniteScrollModule,
		DirectivesModule
	],
	declarations: [
		MeetComponent,
		MeetDirective,
		MeetDirectiveWMS1,
		MeetDirectiveWMS2I,
		MeetSearchComponent,
	],
	exports: [
	]
})
export class MeetModule { }



