import { Component, OnInit, HostListener, Inject } from '@angular/core';
import { DOCUMENT, Title } from "@angular/platform-browser";
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { GoogleContactModalComponent } from '../modals/google-contact-modal/google-contact-modal.component';
import { InvitationSocialComponent } from '../modals/invitation-social/invitation-social.component';

/* Services */
import { SkillsService } from '../../Services/Skills/skills.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { NetworksService } from '../../Services/Networks/networks.service';
import { TokenService } from '../../Services/Authentication/token.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { SharedService } from '../../Services/shared.service';
import { SocialService } from '../../Services/Social/social.service';
import { AccountService } from '../../Services/account.service';
import { MetaService } from '../../Services/meta.service';
import { TrackingService } from '../../Services/Trackings/tracking.service';

/* Animations */
import { popDiscover } from '../../Animations/animations';

export const textScrollMeet = [
	'Let us find who could help you. Join the most exciting community on the Internet.',
	"It won't take more than 20 seconds",
	'Elon and Mark already signed up '
]

export const textScrollMeet1 = [
	'Want to be listed on top?  </br> <p>See how ranking is done in your <a href="/statistics" style="border-bottom: 1px solid">Statistics</a> page</p>',
	'Increase your ranking by 283 % and get 27x more exposure, instantly',
	''
]

declare let gapi: any;

@Component({
	selector: 'app-meet',
	templateUrl: './meet.component.html',
	styleUrls: [
		'./meet.component.scss',
		'../../../public/styles/presentation.scss'],
	providers: [SkillsService, ProfilesService, NetworksService, PicturesService, SocialService, AccountService],
	animations: [popDiscover]
})

export class MeetComponent implements OnInit {

	mobile  	: boolean = false
	showPop 	: boolean = false
	log 		: boolean = false
	banner1 	: boolean = false
	banner2 	: boolean = false
	alreadyPop 	: boolean = false
	notPop 		: boolean = false
	popState 	: string
	text 		: string
	GoogleAuth	: any
	currentApiRequest : any
	isAuthorized : boolean = false
	noBanner 	: boolean = false
	googleCheck : boolean = false

	constructor(private TokenService: TokenService,
				private SharedService: SharedService,
				private NetworksService: NetworksService,
				private ProfilesService: ProfilesService,
				private SocialService: SocialService,
				private dialogService: DialogService,
				private AccountService: AccountService,
				private MetaService: MetaService,
				private Title: Title,
				private TrackingService: TrackingService,
		@Inject(DOCUMENT) private document: any) {
	}

	ngOnInit() {
		this.initWindow();
		this.initToken();
		this.initGoogleInvite();
		this.setMetaData();
		this.trackViewPage();
	}

	trackViewPage() {
		const token = this.TokenService.getToken();

		this.TrackingService.viewActivities({}, token, 7).subscribe(r => {});
	}

	setMetaData() {
		this.Title.setTitle('Witty | Meet');
		this.MetaService.setMeta('description', "Witty is the professional network for the entrepreneurial age. We connect millions of people to what's being built around them, including you.");

		this.MetaService.setMeta('og:title', 'Meet the people you truly need - Witty');
		this.MetaService.setMeta('og:description', 'Meet thousands of designers, programmers, engineers and creative people ready to help you grow your project.');
		this.MetaService.setMeta('og:url', 'https://www.wittycircle.com/meet');
		this.MetaService.setMeta('og:image', 'http://res.cloudinary.com/dqpkpmrgk/image/upload/v1508448688/Share_Link_Cards_Facebook/Thanks_But_No_Thanks_Sheep_Head.png');	}

	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	initToken() {
		if (this.TokenService.getToken() && this.TokenService.getToken().user) {
			this.initProfile(this.TokenService.getToken().user['id']);
			this.log 	= true
		}
	}

	initProfile(id) {
		this.ProfilesService.getProfile(id).subscribe( res => {
			if (res)
				textScrollMeet1[2] = '<img style="width: 45px; border-radius: 50%;" src="' + res.profile.picture + '" alt="profile_picture"/><br /> or share your own <a href="/invite/' + res.profile.invite_link + '" style="border-bottom: 1px solid #fff">invite link</a>'
		});
	}

	initGoogleInvite() {
		if (this.TokenService.getToken()) {
			const my_id = this.TokenService.getToken().user.id

			this.AccountService.getSocialInvite(my_id).subscribe( check => {
				this.googleCheck = check.invite_google
			});
		}
	}

	signIn() {
		this.notShowPop();
		this.SharedService.setSignUpStatus(true);
	}

	notShowPop() {
		this.notPop 	= true
		this.popState 	= ''
		this.showPop 	= false
		this.alreadyPop = false

		setTimeout(() => {
			this.notPop = false
		}, 1000 * 10)
	}

	closeModal() {
		this.popState 	= ''
		setTimeout( () => {
			this.noBanner = true;
		}, 200)
	}

	// showSocialModal(index) {
	// 	this.popState 	= ''
	// 	let disposable 	= this.dialogService.addDialog(GoogleContactModalComponent, {
 //        	profile 	: {},
 //        	index 		: index
 //    	}).subscribe( success => {
 //    		console.log(success);
 //    	});
	// }

	showInvitationModal() {
		window.scrollTo(0, 0);
		this.notShowPop();

		let disposable 	= this.dialogService.addDialog(InvitationSocialComponent, {
        	check : true
    	}).subscribe( success => {
    		this.notPop = false
    	});
	}

	/* ON SCROLL EVENT */
	@HostListener("window:scroll", [])
	onWindowScroll() {
		let number = this.document.documentElement.scrollTop;

		if (!this.notPop) {
			if (number > 50 && !this.alreadyPop) {
				if (this.popState !== 'up1') {
					this.popState 	= 'up1'
					this.showPop 	= true
					this.alreadyPop = true
				}
			} else if (number <= 50 && this.alreadyPop) {
				this.popState 	= ''
				this.showPop 	= false
				this.alreadyPop = false

			}
		}

		// if (!this.log) {
		// 	this.banner1 = true
		// 	this.banner2 = false
		// 	let number = this.document.body.scrollTop;
		// 	if (number > 50 && number < 300) {
		// 		if (this.popState !== 'up1') {
		// 			this.text 		= textScrollMeet[0]
		// 			this.popState 	= 'up1'
		// 			this.showPop 	= true
		// 		}
		// 	} else if (number > 300 && number < 600) {
		// 		if (this.popState !== 'up2') {
		// 			this.popState 	= 'up2'
		// 			this.text 		= textScrollMeet[1]
		// 		}
		// 	} else if (number > 600 && number < 900) {
		// 		if (this.popState !== 'up3') {
		// 			this.popState 	= 'up3'
		// 			this.text 		= textScrollMeet[2]
		// 		}
		// 	} else if (number <= 50) {
		// 		this.popState 	= ''
		// 		this.showPop 	= false
		// 	}
		// } else {
		// 	if (!this.googleCheck) {
		// 		this.banner1 = false
		// 		this.banner2 = true
		// 		let number = this.document.body.scrollTop;
		// 		if (number > 50 && number < 300) {
		// 			if (this.popState !== 'up1') {
		// 				this.text 		= textScrollMeet1[0]
		// 				this.popState 	= 'up1'
		// 				this.showPop 	= true
		// 			}
		// 		} else if (number > 300 && number < 800) {
		// 			if (this.popState !== 'up2') {
		// 				this.popState 	= 'up2'
		// 				this.text 		= textScrollMeet1[1]
		// 			}
		// 		} else if (number > 800) {
		// 			if (this.popState !== 'up3') {
		// 				this.popState 	= 'up3'
		// 				this.text 		= textScrollMeet1[2]
		// 			}
		// 		} else if (number <= 50) {
		// 			this.popState 	= ''
		// 			this.showPop 	= false
		// 		}
		// 	}
		// }
	}
}



