import { Component, OnInit, ChangeDetectorRef, Input, Inject } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from "@angular/platform-browser";

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';
import { SkillModalComponent } from '../../modals/skill-modal/skill-modal.component';
import { CategoryMobileModalComponent } from '../../modals/mobile-modals/category-mobile-modal/category-mobile-modal.component';
import { SkillMobileModalComponent } from '../../modals/mobile-modals/skill-mobile-modal/skill-mobile-modal.component';
import { LocationMobileModalComponent } from '../../modals/mobile-modals/location-mobile-modal/location-mobile-modal.component';

/* Services */
import { SkillsService } from '../../../Services/Skills/skills.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { NetworksService } from '../../../Services/Networks/networks.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';

/* Models */
import { categoriesMeet } from '../../../Interfaces/Constants/categories-meet-constant';

/* Interfaces */
import { SearchModel } from '../../../Interfaces/search-models';
import { searchProfile2 } from '../../../Interfaces/Constants/search-profile.constant';

/* Librairies */
import * as _ from 'underscore';

@Component({
	selector: 'app-meet-search',
	templateUrl: './meet-search.component.html',
	styleUrls: [
		'./meet-search.component.scss',
		'../../../../public/styles/presentation.scss',
		'../../../../public/styles/profile-card.scss',
		'../../../../public/styles/tooltip.scss'
	]
})
export class MeetSearchComponent implements OnInit {

	@Input() mobile 	: boolean
	categories			: Object[]
	profiles 			: Object[]
	currentCategory 	: string
	placeholder 		: string
	skillName 			: string
	skills 				: Array<any>
	// popularSkills 		: Array<any>
	selectedSkills 		: Array<any>
	searchSkills 		: Array<any>
	networks 			: Array<any>
	location 			: any
	searchBody 			: SearchModel
	limit 				: number
	currentRankType 	: string
	rankType 			: string
	selectedNetwork 	: string
	launched 			: boolean
	searching 			: boolean = false
	currentLocation 	: string
	priorities 			: Array<string> = ['skills', 'about', 'location', 'network']

	constructor(private SkillsService: SkillsService,
		private ProfilesService: ProfilesService,
		private NetworksService: NetworksService,
		private dialogService: DialogService,
		private TokenService: TokenService,
		private PicturesService: PicturesService,
		private ActivatedRoute: ActivatedRoute,
		private ChangeDetectorRef: ChangeDetectorRef,
		@Inject(DOCUMENT) private document: any) {
	}

	ngOnInit() {
		this.initModels();
		this.initServices();
	}

	onScroll() {
		const old_limit = this.limit
		this.limit += 32
		this.getSearchBody('paginate', { value1: 32, value2: this.limit });
	}

	initModels() {
		this.skills 			= []
		this.selectedSkills		= []
		this.networks 			= []
		this.profiles 			= []
		this.limit				= 32
		this.placeholder 		= 'Anything'
		this.currentCategory 	= 'to meet smart people'
		this.currentLocation 	= 'any location'
		this.categories 		= categoriesMeet
		this.searchBody 		= searchProfile2
		this.currentRankType 	= 'magic'
		this.rankType	 		= 'popularity'
	}

	initServices() {
		this.searchBody.paginate.limit 	= 32
		this.getSkills();
		this.getNetworks();
		this.initParams();
	}

	initParams() {
		this.ActivatedRoute.queryParams.subscribe((params: Params) => {
			this.selectedSkills = params.skills ? params.skills.split(',') : []
			this.searchSkills 	= this.selectedSkills[0] ? this.selectedSkills : ['']

			const location = localStorage.getItem('geoLocation');

			if (location)
				this.searchBody.query.members[2].value = location

			if (this.searchSkills[0])
				this.getSearchBody('members', { index: 0, value: this.searchSkills });
			else
				this.searchProfiles(0);
		});
	}

	searchProfiles(index) {
		if (index)
			this.searching = true;

		this.ProfilesService.searchProfiles(this.searchBody).subscribe( res => {
			if (index)
				this.profiles = []
			this.profiles 	= this.profiles.concat(res.profiles);
			this.searching 	= false;
		});
	}

	getSkills() {
		this.SkillsService.getSkills().subscribe( res => {
			this.skills = res.skills
			// this.popularSkills = this.skills.slice(0, 5);
		});
	}

	getNetworks() {
		this.NetworksService.getNetworks('university').subscribe( res => {
			this.networks = res.university;
		});
	}

	getSearchBody(group, member) {
		let i = 0;
		if (group === 'members') {
			this.limit = 32
			this.searchBody.paginate.limit 	= 32
			this.searchBody.paginate.offset = 0
			this.searchBody.query.members[member.index].value = member.value
			this.searchBody.query.priority = this.priorities[member.index];
			i = 1;
		}
		if (group === 'sort') {
			const value = member.value === 'popularity' ? 'rank' : member.value
			this.searchBody.query.sort.field = value
			i = 1;
		}
		if (group === 'paginate') {
			this.searchBody.paginate.limit 	= member.value1,
			this.searchBody.paginate.offset = member.value2
		}
		this.searchProfiles(i);
	}

	// selectSkill(name: string) {
	// 	if (this.selectedSkills.indexOf(name) < 0 && this.selectedSkills.length < 5) {
	// 		this.selectedSkills.push(name);
	// 		this.skillName = null;
	// 		this.getSearchBody('members', {index: 0, value: this.selectedSkills});
	// 	}
	// }

	removeSkill(name: string) {
		this.selectedSkills = _.without(this.selectedSkills, name);
		if (!this.selectedSkills.length)
			this.searchSkills[0] = '';
		this.getSearchBody('members', { index: 0, value: this.searchSkills });
	}

	selectCategory(name: string) {
		this.currentCategory = name

		if (name === 'for anything')
			this.getSearchBody('members', {index: 1, value: ''});
		else if (name === "to share what they're working on")
			this.getSearchBody('members', {index: 1, value: "to share what I'm working on"});
		else
			this.getSearchBody('members', {index: 1, value: name});
	}

	changePlaceholder() {
		if (this.placeholder !== 'add skill') {
			this.placeholder = 'add skill'
		} else {
			this.placeholder = 'Anything'
		}
	}

	getAddress(place: Object) {
		let address = place['address_components'][0].long_name + ', '
		if (!place['address_components'][3])
			address = address + "' ', " + place['address_components'][2].long_name
		else
			address = address + place['address_components'][2].short_name + ', ' + place['address_components'][3].long_name
		
		this.getSearchBody('members', {index: 2, value: address});
	}

	changeRankType() {
		const old_type = this.currentRankType
		this.currentRankType = this.rankType
		this.rankType = old_type
		this.getSearchBody('sort', {value: this.currentRankType});
	}

	selectNetwork(name: string, launched: boolean) {
		this.selectedNetwork = name
		this.launched = launched
		if (launched)
			this.getSearchBody('members', {index: 3, value: name});
	}

	removeNetwork() {
		this.selectedNetwork = ''
	}

	followProfile(id, index) {
		if (!this.TokenService.getToken())
			return this.showLoginPopOver();

		this.ProfilesService.followProfile(id, {}).subscribe( res => {
			// this.profiles[index]['hasLiked'] = res.success
			if (!this.profiles[index]['hasLiked']) {
				this.profiles[index]['hasLiked'] = true
				this.profiles[index]['follower'] += 1
			}
			else {
				this.profiles[index]['hasLiked'] = false
				this.profiles[index]['follower'] -= 1
			}
		});
	}

	/* TRANSFORM */
	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	transformCardStatus(status) {
		if (status === 'to meet smart people')
			return 'Meet smart people'
		else if (status === 'for a full time position')
			return 'Full time position'
		else if (status === 'for an internship')
			return 'Internship'
		else if (status === 'for part time collaboration')
			return 'Collaboration'
		else if (status === "to share what I'm working on")
			return 'Share work'
	}

	/* SHOW MODAL */
	showLoginPopOver() {
		let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

	showSkillModal() {
		let disposable 	= this.dialogService.addDialog(SkillModalComponent, {
			selectedSkills : this.selectedSkills
		}).subscribe((skills: Array<string>) => {
			if (!skills[0])
				this.document.getElementById('wms-2-0').style.display = 'inline-block'

			this.selectedSkills = skills;
			this.searchSkills 	= this.selectedSkills[0] ? this.selectedSkills : ['']

			this.getSearchBody('members', {index: 0, value: this.searchSkills});
		});
	}

	/* SHOW MOBILE MODAL */
	showSkillMobileModal() {
		let disposable = this.dialogService.addDialog(SkillMobileModalComponent, {
			skills: this.skills
		}).subscribe(skills => {
			if (skills) {
				this.selectedSkills = skills
				this.getSearchBody('members', { index: 0, value: skills });
			}
		});
	}

	showCategoryMobileModal() {
		let disposable = this.dialogService.addDialog(CategoryMobileModalComponent, {
			categories: this.categories
		}).subscribe(category => {
			if (category)
				this.selectCategory(category);
		});
	}

	showLocationMobileModal() {
		let disposable = this.dialogService.addDialog(LocationMobileModalComponent, {
			noVariable: 'hello'
		}).subscribe(location => {
			if (location) {
				this.currentLocation = location;
				this.ChangeDetectorRef.detectChanges();
				setTimeout(() => {
					this.getSearchBody('members', {index: 2, value: this.currentLocation});
				}, 500);
			}
		});
	}

}



